import { css } from '@emotion/react';
import { observer } from 'mobx-react-lite';
import React, { useEffect, useRef, useState } from 'react';
import { useCardStore, useGlobalStore, useStore } from 'src/hooks/useStore';
import { buttonBox, styleButton, styleDan } from 'src/style';

function Tools() {
  const { shuffle, flipAll, unflipAll } = useCardStore();
  const { confetti } = useGlobalStore();
  const { initLocalStorage } = useStore();

  const { printState } = useStore();
  const [toggle, setToggle] = useState(false);
  const ref = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const clickHandler = (e: MouseEvent): void => {
      if (!(e.target instanceof HTMLElement)) return;
      const className = ref.current?.className!;
      const isMenuClick = !!e.target.closest(`.${className}`);

      setToggle(isMenuClick);
    };

    if (toggle) window.addEventListener('click', clickHandler);

    return () => {
      window.removeEventListener('click', clickHandler);
    };
  }, [toggle]);

  return (
    <div
      css={css`
        ${styleDan}
      `}
    >
      <div>üòé</div>
      <div
        css={css`
          position: relative;

          button {
            ${buttonBox}
            ${styleButton}
          }
        `}
      >
        <button type="button" onClick={confetti}>
          ÏùëÏõêÌïòÍ∏∞
        </button>

        <button
          type="button"
          onClick={() => {
            setToggle(!toggle);
          }}
          css={css`
            ${toggle &&
            css`
              background-color: #84968d !important;
            `}
          `}
        >
          Î©îÎâ¥
        </button>

        {toggle && (
          <div
            ref={ref}
            css={css`
              position: absolute;
              bottom: 43px;
              right: 0;
              width: 100%;

              button {
                border: 2px solid white;
                margin: 0;
              }
            `}
          >
            <button type="button" onClick={shuffle}>
              Ïπ¥Îìú ÏÖîÌîå
            </button>
            <button type="button" onClick={flipAll}>
              Î™®Îëê Îí§ÏßëÍ∏∞
            </button>
            <button type="button" onClick={unflipAll}>
              ÏõêÏÉÅÎ≥µÍµ¨
            </button>
            <button type="button" onClick={printState}>
              ÎÇ¥Î≥¥ÎÇ¥Í∏∞(copy)
            </button>
            <button type="button" onClick={initLocalStorage}>
              Î°úÏª¨Ïä§ÌÜ†Î¶¨ÏßÄ Ï¥àÍ∏∞Ìôî
            </button>
          </div>
        )}
      </div>
    </div>
  );
}

export default observer(Tools);
